.alta {
    margin-top: 150px;
    background-color:#F8F6F0;
    height: auto;
}

.alta h1 {
    text-align: center;
}

.alta .alta-form {
    background-color: #0041c2;
    width: 80%;
    margin: 20px auto;
    padding: 30px;
    color: white;
    border-radius: 20px;
}

.alta .input-group {
    text-align: center;
    padding: 3px;
}

.alta label {
    color: white;
    text-transform: capitalize;
    display: block;
    margin-bottom: 5px;
}

.alta label[for="envio"] {
    display: inline-block;
    margin: 10px 0;
}

.alta form button {
    margin: 15px auto;
    display: block;

    width: 20%;
    height: 35px;
    background-color: #AFDCEC;
    border-radius: 10px;
    font-size: 20px;
    color: #130f40;
}

.alta input {
    width: 70%;
    height: 30px;
    border-radius: 5px;
    background-color:white;
}

.alta input[type="checkbox"] {
    width: 20px;
    height: 20px;
}

.alta th,td {
    border: 1px solid black;
    width: 80px;
    height: 30px;
    padding: 10px;
    text-align: center;
}
.alta th,td:nth-child(7) {
    text-align: left;
}

.alta table {
    border-collapse: collapse;
    margin: 5px auto;
}

.alta th {
    text-transform: uppercase;
    background-color: #111;
    color: white;
}

.alta h2 {
    text-align: center;
}
.error{
    border-radius: 5px;
    background-color: red;
    padding: 2px 16px;
    text-align: center;
    margin: 0 auto;
    width: fit-content;
}
.error:empty{
    display: none;
}
.triangle{
    width: 0;height: 0;
    margin:0 auto;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-bottom: 10px solid red;
}
td{
    font-size: 16px;
    padding: 5px;
}
tr td:first-child{
    font-weight: bold;
}
td img{
    height: 100px;
}
td button{
    padding: 6px;
    border-radius: 8px;
    margin-bottom: 3px;
    color: white;
}
[id *= 'btnBorrar']{
    background-color: red;
}
[id *= 'btnBorrar']:disabled, [id *= 'btnEditar']:disabled{
    background-color: gray;
}
[id *= 'btnEditar']{
    background-color: blue;
}
#modal{
    width: fit-content;
    padding: 2px 10px;
    background-color: lightblue;
    color: black;
    text-align: center;
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translate(-50%,-50%)
}
#modal:empty{
    display: none;
}
@media only screen and (max-width:1000px) {
    tr{
        font-size: 8px;
        width: fit-content;
        margin: 0;
    }
    td{
        width: fit-content;
    }
    .alta form button{
        width: fit-content;
        padding: 0 16px;
    }
}




















