*{
    margin: 0;
    padding: 0;
}
body {
    margin: 0;
    font-family: "Open Sans", sans-serif;
    max-width: 100vw;
    min-height: 100vh;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

ul, h1, h3, p {
    margin: 0;
}
h1{
    text-align: center;
    margin-top: 20px;
    font-family: roboto;
    font-size: 48px;
    text-shadow: -5px -2px paleturquoise;
}

header {
    width: 100%;
    max-width: 100vw;
    height: auto;
}

header nav {
    width: 100%;
    max-width: 100vw;
    background-color: #0041c2;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    z-index: 2;
}
@keyframes navbar{
    100%{font-size: 1.5em;}
}
header nav ul {
    list-style: none;
    width: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}
header nav ul li a {
    text-decoration: none;
    display: block;
    width: 115px;
    height: 54px;
    color: white;
    border-left: 1px solid #F8F6F0;
    text-align: center;
    line-height: 54px;
    background-color: #0041c2
}
header nav ul li a:last-of-type{
    border-right: 1px solid #F8F6F0;
}
header nav ul li a:hover{
    animation: navbar 1s forwards;
}
header > div {
    max-width: 100%;
    width: 100%;
    display: flex;
    justify-content: start;
    justify-content: center;
    position: absolute;
    top: 54px;
    background-color: #F8F6F0;
}

#logo {
    height: 54px;
    line-height: 54px;
    text-align: center;
    border-bottom: 1px solid #AFDCEC;
    border-left: 1px solid #AFDCEC;
}
#logo img, #boton-carrito img{
    height: 54px;
    width: 9vw;
    max-width: 54px;
    border-bottom: 1px solid #AFDCEC;
    border-right: 1px solid #AFDCEC;
}
#barra-busqueda {
    background-color:#AFDCEC;
    width: 80%;
    height: 54px;
    text-align: center;
    line-height: 54px;
}

#boton-carrito {
    background-color:#0041c2;
    
    height: 54px;
}
#boton-carrito img:hover{
    filter: opacity(0.2);
}

#boton-carrito a {
    text-decoration: none;
    color: black;
}

#barra-busqueda form label {
    background-color: #F8F6F0;
    padding: 4px;
    border-radius: 10px;
}

#barra-busqueda form input {
    background-color: #FBFCF8;
    padding: 2px;
    width: 50%;
}
#barra-busqueda form input[type="submit"] {
    background-color: #0041c2;
    width: 60px;
    color: #F8F6F0;
}
#barra-busqueda a{
    all: unset;
    background-color: aquamarine;
    border-radius: 16px;
    padding: 3px;
    font-weight: bold;
    cursor: pointer;
}
main {
    height: 500px;
}

footer {
    background-color: #B4CFEC;
    color:white;
    height: 80px;
    min-height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 100vw;
    font-size: 22px;
    margin-top: auto;
}
footer > div{
    margin-left: 16px;
}
footer nav{
    height: 100%;
    display: flex;
    align-items: center;
}
footer nav ul li{
    display: inline;
}
footer img{
    height: 24px;
    margin-right: 16px;
}

.msg-error {
    color: red;
}
@media only screen and (max-width:1050px){
    header nav{
        position: relative;
        top: 0;
    }
    header nav ul li a{
        width: fit-content;
        padding: 0 2vw;
    }
    #barra-busqueda{
        width: 80vw;
    }
    #logo img, #boton-carrito img{
        width: 15vw;
    }
    #search{
        display: none;
    }
}