h1{
    margin-top: 50px;
}
.contacto{
    margin-top: 150px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100vw;
    text-align: center;
}
.contacto-form{
    background-color: #0041c2;
    color: white;
    width: 50vw;
    padding: 16px 160px;
    margin: auto;
    margin-top: 32px;
    margin-bottom: 100px;
    border-radius: 16px;
}
.contacto-form label{
    display: block;
    margin: 4px 0;
}
.contacto input, .contacto textarea{
    margin: 4px 0;
    width: 100%;
}
.contacto input{
    height: 24px;
}
.contacto button{
    margin-top: 8px;
    padding: 8px 16px;
    background-color: lightblue;
    border-radius: 10px;
}
@media only screen and (max-width:1000px){
    main{
        margin-top: 80px;
    }
}