#volver{
    background-color: aquamarine;
    border-radius: 16px;
    padding: 10px;
    text-decoration: none;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}
#paginaProducto{
    display: flex;
    justify-content: center;
    width: 50%;
    margin: auto;
    margin-top: 20px;
}
img{
    border-radius: 8px;
}
#informacionProducto{
    display: flex;
    flex-direction: column;
}
#informacionProducto div:first-child{
    font-size: 48px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
#informacionProducto section{
    display: flex;
    flex-direction: column;
    text-align: justify;
    align-items: baseline;
    padding-left: 32px;
    width: fit-content;
    min-width: 0;
    padding-right: 120px;
    white-space: nowrap;
}
#informacionProducto section div span{
    font-weight: bold;
}
#informacionProducto > div:first-of-type{
    text-align: right;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    margin-bottom: 16px;
}
#detail{
    margin: auto;
    margin-top: 20px;
    width: 60%;
    border: 1px solid gray;
    padding: 8px;
}
#productoPage button{
    all:unset;
    margin-left: 60%;
    background-color: aquamarine;
    padding: 5px;
    border-radius: 16px;
    font-weight: bold;
}