.carrito{
    margin-top: 100px;
}
button[id *= 'btnAgregar-']{
    background-color: green;
}
button[id *= 'btnRestar-']{
    background-color: red;

}
.carrito{
    width: 100%;
}
.carrito table{
    margin: auto;
}
.carrito > div{
    display: flex;
    justify-content: center;
    width: 100%;
    font-size: 64px;
}
.carrito > div span{
    color: blue;
}
.carrito img{
    width: 150px;
}
.carrito button:first-of-type{
    all: unset;
    display: flex;
    justify-content: center;
    margin: auto;
    border: 2px solid black;
    padding: 5px;
    border-radius: 16px;
    color: white;
    background-color: red;
}
.carrito .buttons div:first-of-type{
    display: flex;
    justify-content: center;
}
.carrito .buttons button{
    margin: 5px;
}
.carrito .buttons > button{
    margin: auto;
}
#empty{
    margin-top: 10px;
    font-size: 48px;
    width: 100%;
    display: flex;
    justify-content: center;
}