#root{
    display: flex;
    flex-direction: column;
    height: 100vh;
    min-height: 100vh;
}
main {
    margin: auto;
    margin-top: 150px;
    margin-bottom: 50px;
    width: 100vw;
    height: fit-content;
    justify-content: center;
    max-width: 100%;
}
main .section-cards{
    display: flex;
    justify-content: center;
    width: 100%;
}
main .section-cards-container {
    width: fit-content;
    max-width: 100vw;
    display: grid;
    grid-template-columns: 500px 500px 500px;
    gap: 20px;
}

main section img {
    width: 200px;
    height: 200px;
    object-fit: scale-down;
    margin-right: 35px;}

main section {
    display: flex;
    margin: 10px;
    align-items: center;
    min-width: 500px;
    cursor: default;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 16px;
    overflow:hidden;
}
@keyframes blueShadow {
    50%{box-shadow: 10px 5px lightblue, 
        -10px 5px lightblue}
    100%{box-shadow:0 0 lightblue}
}
[id *= 'producto-']:hover{
    animation: blueShadow 15s infinite;
}
[id *= 'producto-'] div h3{
    margin-top: 0;
    font-family: 'Times New Roman', Times, serif;
    font-size: 24px;
}
[id *= 'producto-'] div p{
    max-width: 15ch;
    overflow: hidden;
    text-overflow: ellipsis;
}
[id *= 'producto-'] div p b{
    font-family: Arial, Helvetica, sans-serif;
}
main header{
    text-align: center;
    margin-bottom: 36px;
}
[id *= 'flexButton']{
    display: flex;

}
.flexButton{
    display: flex;
    align-items: center;
}
.button-carrito-add{
    color: white;
    background-color: #0041c2;
    width: fit-content;
    padding: 10px;
    margin-top: 16px;
    border-radius: 16px;
}
#button-carrito-add{
    border: none;
    width: 100%;
    display: flex;
    justify-content: center;
}
button{
    cursor: pointer;
    margin-right: 6px;
}
#modal{
    background-color: lightblue;
    color: black;
    left: 50%;
    transform: translate(-50%,-50%);
    position: fixed;
}
#modal:empty{
    display: none;
}
[id *= 'goTo-' ]{
    background-color: aquamarine;
    padding: 10px;
    height: fit-content;
    margin-top: 16px;
    margin-right: 3px;
    border-radius: 32px;
    text-decoration: none;
}
[id *= 'goTo-' ] div{
    transform: rotate(-45deg);
}

@media only screen and (max-width: 1580px){
    main{
        margin-top: 100px;
    }
    main .section-cards-container{
        grid-template-columns: 500px 500px;
    }
}
@media only screen and (max-width: 1065px) {
    main .section-cards-container{
        grid-template-columns: 300px 300px;
    }
    main section{
        flex-direction: column;
        min-width: 300px;
        padding-bottom: 16px;
    }
    main section img{
        margin:auto;
    }
}
@media only screen and (max-width: 700px) {
    main .section-cards-container{
        grid-template-columns: 300px;
    }
}
@media only screen and (min-width: 1065px){
    .section-cards-container button{
        margin-bottom: 16px;
    }
    [id *= 'producto-'] .flexButton{
        margin-left: 16px;
        flex-direction: column;
    }
}
@media only screen and (min-width: 1065px) {
    .section-cards-container h3{
        margin-top: 16px;
    }
}
